/* ==== NAVIGATION CONTAINER ==== */
#root > div > div.nav-container {
  background-color: #201f1e;
  opacity: 0.97;
  font-family: 'Roboto Slab', Arial, Helvetica, sans-serif;
  position: -webkit-sticky;
  position: sticky;
  top: -7px;
  z-index: 100;
  margin-bottom: 3%;
  padding: 0 2em;
}
.nav-container::before {
  content: '';
  width: 100%;
  position: absolute;
  background: linear-gradient(to right, #1a83a1, #675899);
  height: 0.8px;
  left: 0;
  bottom: 0;
}
