#ClubeSlideInner-container {
  padding: 20px;
  width: 100%;
  height: 600px;
}
#ClubeSlideOuter-swiper-slide {
  border-radius: 13px;
  font-family: 'Roboto Slab', sans-serif;
  text-align: center;
  font-size: 18px;
  background-position: center;
  background-size: cover;
  width: 500px;
  height: 700px;
  background-color: #20201f;
  color: #fffbfb;
  padding: 1px;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#ClubeSlideInner-swiper-slide {
  border: 2px solid;
  border-image: linear-gradient(
    90deg,
    rgba(0, 176, 227, 1) 48%,
    rgba(180, 160, 255, 1) 100%
  );

  border-image-slice: 1;
}

#ClubeSlideOuter-swiper-button-next {
  background-image: url(data:image/png;base64,) !important;
  height: 130px;
  width: 40px;
  background-repeat: no-repeat;
  background-size: cover;
  content: none;
}
#ClubeSlideOuter-swiper-button-next:focus {
  outline: none;
}

#ClubeSlideOuter-swiper-button-prev {
  background-image: url(data:image/png;base64,) !important;
  height: 130px;
  width: 40px;
  background-repeat: no-repeat;
  background-size: cover;
  content: unset;
}

#ClubeSlideOuter-swiper-button-prev:focus {
  outline: none;
}
#ClubeSlideOuter-swiper-button-prev::after {
  content: '';
}
#ClubeSlideOuter-swiper-button-next::after {
  content: '';
}
#ClubeSlide-Header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
}
#ClubeSlide-logo-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
#ClubeSlide-cordinator {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 5px;
}
#ClubeSlide-club-title {
  padding: 0%;
  font-family: 'Roboto Slab';
  font-size: 40px;
  font-weight: 300;
  background: linear-gradient(to right, #00bcf2, #b4a0ff);
  letter-spacing: -0.015em;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-clip: text;
}

#ClubeSlide-description {
  font-family: 'Roboto Slab', sans-serif;
  font-style: normal;
  font-weight: 300;
  margin: auto 1.5%;

