  -webkit-text-fill-color: transparent;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-clip: text;
}

#ClubeSlide-btn {
  border: 2px solid;
  border-image: linear-gradient(to right, #00bcf2, #b4a0ff, #01b295);
  border-image-slice: 1;
  color: #fff5f5;
  font-size: 21px;
  margin-bottom: 5px;
